import CApi from "@api";
import { CCardButtonReportAudit, CTableReport } from "@components";
import { CError, CLoadingSpinner, CPageTitle, CTitle } from "@components";
import { FGetAccess } from "@function";
import { Card, Col, Row, Space } from "antd";
import { useRouter } from "next/router";
import React, { useEffect, useState } from "react";
import * as XLSX from "xlsx";
import jsPDF from "jspdf";
import autoTable from "jspdf-autotable";

const ReportAudit = () => {
  const date = new Date();

  let day = date.getDate();
  let month = date.getMonth() + 1;
  let year = date.getFullYear();
  let currentDate = `${day}-${month}-${year}`;

  const router = useRouter();
  const [total, setTotal] = useState(0);
  const [loading, setLoading] = useState(false);
  const [loadData, setLoadData] = useState(false);
  const [data, setData] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [accessList, setAccessList] = useState({
    viewAny: true,
    view: true,
    create: true,
    update: true,
    delete: true,
  });
  const [paginate, setPaginate] = useState({
    pageSize: 15,
    current_page: 1,
    keyword: "",
  });
  const [forms, setForms] = useState({
    start: "",
    end: "",
    tag_number: "",
  });
  useEffect(() => {
    FGetAccess(router, setAccessList, setIsLoading);
  }, []);
  useEffect(() => {
    loadData ? getData() : false;
  }, [paginate]);

  const columns = [
    {
      title: "Barcode",
      dataIndex: "tag_number",
      key: "tag_number",
      align: "center",
      ellipsis: true,
    },
    {
      title: "No Induk Barang",
      dataIndex: "no_induk_barang",
      key: "no_induk_barang",
      align: "center",
      ellipsis: true,
    },
    {
      title: "No Barang",
      dataIndex: "no_barang",
      key: "no_barang",
      align: "center",
      ellipsis: true,
    },
    {
      title: "Name",
      key: "name",
      width: 350,
      align: "center",
      ellipsis: true,
      dataIndex: "name",
    },
    {
      title: "Branch",
      key: "branch",
      dataIndex: "branch",
      align: "center",
      ellipsis: true,
    },
    {
      title: "Departement",
      key: "vendor",
      dataIndex: "vendor",
      align: "center",
      ellipsis: true,
    },
    {
      title: "Ownership",
      key: "ownership",
      dataIndex: "ownership",
      align: "center",
      ellipsis: true,
    },
    {
      title: "Code Activa",
      key: "code_activa",
      dataIndex: "code_activa",
      align: "center",
      ellipsis: true,
    },
    {
      title: "Location",
      align: "center",
      key: "location",
      dataIndex: "location",
      ellipsis: true,
    },
    {
      title: "Tanggal Perolehan",
      dataIndex: "tgl_perolehan",
      key: "tgl_perolehan",
      align: "center",
      ellipsis: true,
    },
    {
      title: "Jenis Barang",
      key: "asset_type",
      dataIndex: "asset_type",
      align: "center",
      ellipsis: true,
    },
    {
      title: "Payment Voucher",
      key: "payment_voucher",
      dataIndex: "payment_voucher",
      align: "center",
      ellipsis: true,
    },
    {
      title: "Merek Barang",
      key: "merek_barang",
      dataIndex: "merek_barang",
      align: "center",
      ellipsis: true,
    },
    {
      title: "Model",
      key: "model",
      dataIndex: "model",
      align: "center",
      ellipsis: true,
    },
    {
      title: "Harga Perolehan",
      key: "harga_perolehan",
      align: "center",
      ellipsis: true,
      render: (item) => {
        return item.currency_perolehan == "IDR"
          ? new Intl.NumberFormat("id-ID", {
              style: "currency",
              currency: "IDR",
            }).format(item.harga_perolehan)
          : new Intl.NumberFormat("en-US", {
              style: "currency",
              currency: "USD",
            }).format(item.harga_perolehan);
      },
    },
    {
      title: "Currency Perolehan",
      dataIndex: "currency_perolehan",
      key: "currency_perolehan",
      align: "center",
      ellipsis: true,
    },
    {
      title: "Serial Number",
      key: "serial_number",
      dataIndex: "serial_number",
      align: "center",
      ellipsis: true,
    },
    {
      title: "Anggaran Asset",
      key: "anggaran_asset",
      dataIndex: "anggaran_asset",
      align: "center",
      ellipsis: true,
    },
    {
      title: "Masa Manfaat (Year)",
      dataIndex: "masa_manfaat",
      key: "masa_manfaat",
      align: "center",
      ellipsis: true,
      render: (item) => {
        return item === null ? "0" : item;
      },
    },

    {
      title: "Flag",
      dataIndex: "flag",
      key: "flag",
      align: "center",
      ellipsis: true,
    },
    {
      title: "Status Activa",
      key: "status_activa",
      dataIndex: "status_activa",
      align: "center",
      ellipsis: true,
    },
    {
      title: "Remark",
      key: "remark",
      dataIndex: "remark",
      align: "center",
      ellipsis: true,
    },
    {
      title: "Updated By",
      dataIndex: "updated_by",
      key: "updated_by",
      align: "center",
      ellipsis: true,
    },
    {
      title: "Kondisi Barang",
      key: "status",
      dataIndex: "status",
      fixed: "right",
      align: "center",
      width: 150,
      ellipsis: true,
      render: (text, record) => {
        return {
          props: {
            style: {
              background: record.before ? "#fc8282" : "#afdc82",
            },
          },
          children: text,
        };
      },
    },
    {
      title: "Create Date",
      dataIndex: "date",
      key: "date",
      fixed: "right",
      ellipsis: true,
      width: 100,
      render: (text, record) => {
        return {
          props: {
            style: {
              background: record.before ? "#fc8282" : "#afdc82",
            },
          },
          children: text,
        };
      },
    },
  ];

  const onShowSizeChange = (current, pageSize) => {
    setPaginate({
      ...paginate,
      pageSize: pageSize,
      current_page: current,
    });
  };
  const onChangeStartDate = (value, dateString) => {
    setForms({
      ...forms,
      start: dateString,
    });
  };
  const onChangeEndDate = (value, dateString) => {
    setForms({
      ...forms,
      end: dateString,
    });
  };

  //* Crud
  const onReset = () => {
    setForms({
      ...forms,
      start: "",
      end: "",
      tag_number: "",
    });
    setPaginate({
      pageSize: 15,
      current_page: 1,
      keyword: "",
    });
    getData();
  };

  const getData = async () => {
    setLoading(true);
    const params = {
      barcode: forms.tag_number,
      startDate: forms.start,
      endDate: forms.end,
      page: paginate.current_page,
      keyword: forms.keyword,
      per_page: paginate.pageSize,
    };
    await CApi.get("api/report/audit", { params }).then((res) => {
      setLoadData(true);
      setLoading(false);
      setData(res.data.data);
      setTotal(res.data.meta.total);
    });
  };
  const exportExcel = async () => {
    const params = {
      barcode: forms.tag_number,
      startDate: forms.start,
      endDate: forms.end,
    };

    await CApi.get("api/report/audit/excel", {
      params: params,
    }).then((res) => {
      const data = res.data;
      const fileName = "Report-Audit " + currentDate + ".xlsx";
      const worksheet = XLSX.utils.json_to_sheet(data);
      /* choose columns (with header) which one you convert into text format*/
      var tgl_perolehan = XLSX.utils.decode_col("K");
      var harga = XLSX.utils.decode_col("P");
      var tgl_created = XLSX.utils.decode_col("Z");
      /*also use these formats  " b Boolean, e Error, n Number, d Date, s Text, z Stu,"*/
      var range = XLSX.utils.decode_range(worksheet["!ref"]);
      for (var i = range.s.r + 1; i <= range.e.r; ++i) {
        /* find the data cell (range.s.r + 1 skips the header row of the worksheet) */
        var tgl_perolehanToDate = XLSX.utils.encode_cell({
          r: i,
          c: tgl_perolehan,
        });
        var hargaToNum = XLSX.utils.encode_cell({
          r: i,
          c: harga,
        });
        var createdDate = XLSX.utils.encode_cell({
          r: i,
          c: tgl_created,
        });

        /* if the particular row did not contain data for the column, the cell will not be generated */
        /* assign the `.z` Text format */
        worksheet[tgl_perolehanToDate].t = "d";
        worksheet[hargaToNum].t = "n";
        worksheet[hargaToNum].z = "0,000.00";
        worksheet[createdDate].t = "d";
      }

      const workbook = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(workbook, worksheet, "Sheet1");
      XLSX.writeFile(workbook, fileName);
    });
  };
  const exportPdf = async () => {
    const params = {
      barcode: forms.tag_number,
      startDate: forms.start,
      endDate: forms.end,
    };

    await CApi.get("api/report/audit/pdf", {
      params: params,
    }).then((res) => {
      generateLetterPdf(res.data);
    });
  };

  const generateLetterPdf = async (data) => {
    const doc = new jsPDF("landscape", "px", "a4", "false");
    let text = "Audit";
    let xOffset =
      doc.internal.pageSize.width / 2 -
      (doc.getStringUnitWidth(text) * doc.internal.getFontSize()) / 2;
    doc.text(text, xOffset, 20);
    autoTable(doc, {
      styles: { fontSize: 5 },
      margin: { right: 5, left: 5 },
      styles: { fontSize: 5 },
      columnStyles: {
        number: { cellWidth: 12, fontSize: 3.8 },
        tag_number: { cellWidth: 25, fontSize: 3.8 },
        no_barang: { cellWidth: 20, fontSize: 3.8 },
        no_induk_barang: { cellWidth: 20, fontSize: 3.8 },
        name: { cellWidth: 65, fontSize: 3.8 },
        branch: { cellWidth: 20, fontSize: 3.8 },
        vendor: { cellWidth: 35, fontSize: 3.8 },
        code_activa: { cellWidth: 25, fontSize: 3.8 },
        location: { cellWidth: 25, fontSize: 3.8 },
        tgl_perolehan: { cellWidth: 25, fontSize: 3.8 },
        asset_type: { cellWidth: 22, fontSize: 3.8 },
        payment_voucher: { cellWidth: 25, fontSize: 3.8 },
        merek_barang: { cellWidth: 22, fontSize: 3.8 },
        model: { cellWidth: 22, fontSize: 3.8 },
        currency: { cellWidth: 25, fontSize: 3.8 },
        currency_perolehan: { cellWidth: 25, fontSize: 3.8 },
        serial_number: { cellWidth: 21, fontSize: 3.8 },
        anggaran_asset: { cellWidth: 30, fontSize: 3.8 },
        masa_manfaat: { cellWidth: 25, fontSize: 3.8 },
        flag: { cellWidth: 17, fontSize: 3.8 },
        status_activa: { cellWidth: 20, fontSize: 3.8 },
        remark: { cellWidth: 25, fontSize: 3.8 },
        status: { cellWidth: 22, fontSize: 3.8 },
        updated_by: { cellWidth: 25, fontSize: 3.8 },
        created_at: { cellWidth: 25, fontSize: 3.8 },
      },
      margin: { right: 5, left: 5 },
      body: data,
      columns: [
        { header: "#", dataKey: "number" },
        { header: "Barcode", dataKey: "tag_number" },
        { header: "No. Barang", dataKey: "no_barang" },
        { header: "No. Induk Barang", dataKey: "no_induk_barang" },
        { header: "Name", dataKey: "name" },
        { header: "Branch", dataKey: "branch" },
        { header: "Departement", dataKey: "vendor" },
        { header: "Code Activa", dataKey: "code_activa" },
        { header: "Location", dataKey: "location" },
        { header: "Tgl. Perolehan", dataKey: "tgl_perolehan" },
        { header: "Jenis Barang", dataKey: "asset_type" },
        { header: "Payment Voucher", dataKey: "payment_voucher" },
        { header: "Merk", dataKey: "merek_barang" },
        { header: "Model", dataKey: "model" },
        { header: "Harga Perolehan", dataKey: "currency" },
        { header: "Currency Perolehan", dataKey: "currency_perolehan" },
        { header: "S/N", dataKey: "serial_number" },
        { header: "Anggaran Asset", dataKey: "anggaran_asset" },
        { header: "Masa Manfaat", dataKey: "masa_manfaat" },
        { header: "Flag", dataKey: "flag" },
        { header: "Status Activa", dataKey: "status_activa" },
        { header: "Kondisi Barang", dataKey: "status" },
        { header: "Remark", dataKey: "remark" },
        { header: "Updated By", dataKey: "updated_by" },
        { header: "Created", dataKey: "created_at" },
      ],
    });
    doc.save("Report Audit " + currentDate + ".pdf");
  };
  const handleChange = (evt) => {
    const newData = { ...forms };
    newData[evt.target.id] = evt.target.value;
    setForms(newData);
  };

  //* End Crud

  if (isLoading) return <CLoadingSpinner />;
  if (!accessList.viewAny) return <CError status={403} />;

  return (
    <Row>
      <CTitle />
      <Col span={24}>
        <Card
          cover={
            <Col
              span={24}
              style={{ backgroundColor: "#1890ff", paddingTop: "5px" }}
            />
          }
        >
          <Row>
            <CPageTitle />
          </Row>
          <Row style={{ width: "100%" }}>
            <Col span={24}>
              <CCardButtonReportAudit
                onChangeStartDate={onChangeStartDate}
                onChangeEndDate={onChangeEndDate}
                forms={forms}
                onChangeBarcode={handleChange}
                handleDownloadExcel={exportExcel}
                handleDownloadPDF={exportPdf}
                handleGetData={getData}
                handleReset={onReset}
              />
            </Col>
          </Row>
          <CTableReport
            columns={columns}
            data={data}
            loading={loading}
            scroll={{ x: 4000, y: 350 }}
            total={total}
            onShowSizeChange={onShowSizeChange}
            current={paginate.current_page}
            rowClassName={(record, index) =>
              record.before ? "bg-red" : "bg-green"
            }
          />
        </Card>
      </Col>
    </Row>
  );
};

export default ReportAudit;
